.traveler-card {
  position: relative;
  max-width: 285px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  &:hover {
    filter: drop-shadow(0 4px 9px rgba(0, 0, 0, 0.16));
  }
}

.traveler-card:hover .traveler-card__img img {
  transform: scale(1.1);
}

.traveler-card__img {
  width: 270px;
  height: 270px;
  border-radius: 10px 10px 0 0;
  overflow: hidden;
}

.traveler-card__img img {
  width: 270px;
  height: auto;
  display: block;
  border-radius: 10px 10px 0 0;
  transition: all 0.3s ease;
}

.traveler-card__info {
  position: relative;
  padding: 27px 20px 17px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: $white-basic;
  border-radius: 0 0 10px 10px;
}

.traveler-card__flags {
  position: absolute;
  top: -9px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
}

.traveler-card__flags li:not(:last-child) {
  margin-right: 20px;
}

.traveler-card__flags span,
.traveler-card__transport span {
  display: none;
}

.traveler-card__title {
  max-width: 250px;
  margin-bottom: 15px;
  padding: 0 15px;
}

.traveler-card__hashtags {
  margin-bottom: 7px;
}

.traveler-card__progress {
  position: absolute;
  top: 17px;
  left: 17px;
  width: 60px;
  height: 60px;
}

@media (min-width: $tablet-width) {
  .traveler-card__img {
    width: 285px;
    height: 285px;
    border-radius: 20px 20px 0 0;
  }

  .traveler-card__img img {
    width: 100%;
  }

  .traveler-card__info {
    padding: 33px 20px 21px;
    border-radius: 0 0 20px 20px;
  }

  .traveler-card__title {
    margin-bottom: 23px;
    padding: 0 24px;
    font-size: 24px;
    line-height: 1;
  }

  .traveler-card__hashtags {
    margin-bottom: 20px;
  }

  .traveler-card__progress {
    top: 21px;
    left: 21px;
  }
}

@media (min-width: $desktop-width) {
  .traveler-card__flags {
    top: -12px;
  }
}
