@charset "UTF-8";
/* GLOBAL */
@font-face {
  font-family: "Blogger Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/bloggersans-light.woff2") format("woff2"), url("../fonts/bloggersans-light.woff") format("woff");
}
@font-face {
  font-family: "Blogger Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/bloggersans.woff2") format("woff2"), url("../fonts/bloggersans.woff") format("woff");
}
@font-face {
  font-family: "Blogger Sans";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/bloggersans-medium.woff2") format("woff2"), url("../fonts/bloggersans-medium.woff") format("woff");
}
@font-face {
  font-family: "Blogger Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/bloggersans-bold.woff2") format("woff2"), url("../fonts/bloggersans-bold.woff") format("woff");
}
/* WHITE / BLACK */
/* GREY */
/* BLUE */
/* YELLOW / ORANGE / RED / GREEN */
/* VIEWPORTS */
html {
  height: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

body {
  position: relative;
  min-width: 320px;
  max-width: 1440px;
  min-height: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  font-family: "Blogger Sans", "Helvetica", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.4;
  background: #edeff6;
  color: #444444;
}

.main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* COMPONENTS */
.container {
  margin: 0 auto;
  padding: 0 25px;
}

@media (min-width: 768px) {
  .container {
    padding: 0 40px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0 60px;
  }
}
.title {
  margin: 0;
  font-family: "Blogger Sans", "Helvetica", sans-serif;
  font-weight: 700;
  font-size: 36px;
  line-height: 1;
  color: #ffffff;
}

@media (min-width: 768px) {
  .title {
    font-size: 60px;
  }
}
.subtitle {
  margin: 0;
  font-family: "Blogger Sans", "Helvetica", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 1;
  color: #ff8d30;
}
.subtitle--form {
  color: #1d2e5b;
}
.subtitle--catalog {
  font-size: 22px;
  letter-spacing: 0.02em;
  color: #1d2e5b;
}

@media (min-width: 768px) {
  .subtitle {
    font-size: 45px;
  }
  .subtitle--form {
    font-size: 36px;
  }
}
.title-card {
  position: relative;
  display: block;
  font-weight: 700;
  font-size: 21px;
  line-height: 1;
  color: #1d2e5b;
  outline: none;
}
.title-card:hover, .title-card:focus {
  opacity: 0.6;
}
.title-card:active {
  opacity: 0.3;
}
.title-card::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 6px;
  height: 6px;
  background-color: #f97676;
  border-radius: 50%;
  content: "";
}
.title-card--catalog::before {
  top: 9px;
  left: unset;
  right: 0;
}

.title-card--online::before {
  background-color: #81e31e;
}

@media (min-width: 768px) {
  .title-card {
    max-width: 255px;
    padding-right: 30px;
  }
  .title-card::before {
    top: 10px;
    width: 8px;
    height: 8px;
  }
}
@media (min-width: 1440px) {
  .title-card {
    max-width: 240px;
  }
  .title-card::before {
    left: -23px;
  }
}
.button {
  width: 100%;
  display: block;
  text-align: center;
  font-family: "Blogger Sans", "Helvetica", sans-serif;
  font-weight: 700;
  font-size: 17px;
  line-height: 1;
  color: #1d2e5b;
  background-color: #ffd74b;
  outline: none;
  text-transform: uppercase;
}
.button:hover, .button:focus {
  background-color: #ffbd30;
}
.button:active {
  color: rgba(29, 46, 91, 0.3);
}
.button--main {
  min-width: 270px;
  padding: 23px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
  border-radius: 10px;
  text-transform: none;
}
.button--main::after {
  width: 11px;
  height: 13px;
  background-image: url("../img/arrow-right.svg");
  background-repeat: no-repeat;
  background-size: contain;
  content: "";
}
.button--form {
  min-width: 220px;
  padding: 6px 42px;
  line-height: 1.7;
  border-radius: 20px;
}
.button--catalog {
  min-width: 230px;
  padding: 12px 78px 11px;
  color: #161c35;
  border-radius: 25px;
}
.button--auth {
  padding: 11px 80px 10px;
  line-height: 1.7;
  color: #161c35;
  border-radius: 25px;
}

.button--main:active::after {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .button--catalog {
    min-width: 150px;
    width: 150px;
    padding: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
  }

  .button--form::after {
    width: 14px;
    height: 13px;
    background-image: url("../img/arrow-right.svg");
    background-repeat: no-repeat;
    background-size: contain;
    content: "";
  }

  .button--form:active::after {
    opacity: 0.3;
  }
}
.transport {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.transport--catalog .transport__item, .transport--filter .transport__item {
  width: 32px;
  height: 32px;
  background-color: #edeff6;
  border-radius: 50%;
}
.transport--catalog .transport__item::after, .transport--filter .transport__item::after {
  top: 40px;
}
.transport--catalog .transport__item::before, .transport--filter .transport__item::before {
  top: 35px;
}
.transport--filter .transport__item {
  width: 32px;
  height: 32px;
  background-color: #ffffff;
  border-radius: 50%;
  cursor: pointer;
}
.transport--filter .transport__item:hover {
  filter: drop-shadow(0 2px 6px rgba(22, 28, 53, 0.17));
}
.transport--filter .transport__item:active {
  background-color: #e2e5f2;
  filter: none;
}

.transport__input:focus + svg {
  outline: 1px solid #ffbd30;
  border-radius: 4px;
}

.transport__item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 33px;
  height: 33px;
}
.transport__item svg {
  opacity: 0.15;
}
.transport__item > input:checked + svg {
  opacity: 1;
}
.transport__item > input:checked + svg:hover, .transport__item > input:checked + svg:focus {
  opacity: 0.7;
}
.transport__item > input:checked + svg:active {
  opacity: 0.3;
}
.transport__item--active svg {
  opacity: 1;
}
.transport__item--plane svg {
  width: 21px;
  height: 21px;
}
.transport__item--bus svg {
  width: 17px;
  height: 19px;
}
.transport__item--bike svg {
  width: 24px;
  height: 24px;
}
.transport__item--run svg {
  width: 21px;
  height: 23px;
}
.transport__item::after {
  position: absolute;
  top: -37px;
  padding: 10px;
  font-weight: 400;
  font-size: 16px;
  line-height: 1;
  text-align: center;
  color: #ffffff;
  background-color: #101d41;
  border-radius: 4px;
  display: none;
  content: attr(data-transport);
}
.transport__item::before {
  position: absolute;
  top: -5px;
  width: 10px;
  height: 10px;
  background-color: #101d41;
  transform: rotate(45deg);
  display: none;
  content: "";
}

.transport__item:hover::after, .transport__item:hover::before {
  display: block;
}

@media (min-width: 768px) {
  .transport--catalog {
    flex-direction: column;
  }

  .transport__item svg {
    width: 23px;
    height: 22px;
  }
  .transport__item::before {
    top: 27px;
  }
  .transport__item::after {
    top: 32px;
  }

  .transport--filter .transport__item {
    width: 44px;
    height: 44px;
  }
  .transport--filter .transport__item::after {
    top: 50px;
  }
  .transport--filter .transport__item::before {
    top: 45px;
  }

  .transport--catalog .transport__item {
    background-color: transparent;
  }
  .transport--catalog .transport__item::after {
    top: 0;
    right: 40px;
  }
  .transport--catalog .transport__item::before {
    top: 12px;
    right: 35px;
  }
}
@media (min-width: 1440px) {
  .transport--catalog {
    flex-direction: row;
  }

  .transport--catalog .transport__item::before {
    top: 35px;
    right: unset;
  }
  .transport--catalog .transport__item::after {
    top: 40px;
    right: unset;
  }
}
.hashtags {
  margin: 0;
  padding: 0;
  list-style: none;
  max-width: 190px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.hashtags--catalog {
  padding: 0 5px;
  justify-content: flex-start;
}

.hashtags__item {
  margin-right: 3px;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.1;
  color: #444444;
}

@media (min-width: 768px) {
  .hashtags {
    max-width: 240px;
  }

  .hashtags__item {
    margin-right: 6px;
    font-size: 20px;
    line-height: 1.1;
  }
}
.progress {
  position: relative;
}
.progress--index {
  width: 60px;
  height: 60px;
}
.progress--catalog {
  width: 46px;
  height: 46px;
}
.progress--form {
  width: 100px;
  height: 100px;
}

.progress__result {
  position: absolute;
  top: 58%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
}
.progress__result--form {
  top: 48%;
}

.progress__result-num {
  font-weight: 700;
  font-size: 24px;
  line-height: 1;
  color: #ffffff;
}
.progress__result-num--catalog {
  color: #1d2e5b;
}
.progress__result-num--form {
  font-size: 38px;
  color: #161c35;
}

.progress__result-note {
  position: relative;
  font-weight: 400;
  font-size: 14px;
  line-height: 1;
  color: #ffffff;
}
.progress__result-note::before {
  position: absolute;
  top: -2px;
  left: -2px;
  width: 30px;
  height: 1px;
  background-color: #ffffff;
  opacity: 0.3;
  content: "";
}
.progress__result-note--catalog {
  display: none;
}
.progress__result-note--form {
  padding-top: 4px;
  font-size: 18px;
  color: #444444;
}
.progress__result-note--form::before {
  left: -10px;
  width: 51px;
  background-color: #444444;
  opacity: 0.3;
}

.progress__level {
  fill: none;
  stroke: #ff8d30;
  stroke-width: 3px;
  stroke-linecap: round;
  transform: rotate(-90deg);
  transform-origin: center;
}
.progress__level--index {
  fill: rgba(0, 0, 0, 0.6);
  stroke-width: 4px;
}

@media (min-width: 768px) {
  .progress--catalog {
    width: 60px;
    height: 60px;
  }
  .progress--catalog svg {
    width: 60px;
    height: 60px;
  }
  .progress--form {
    width: 95px;
    height: 95px;
  }
  .progress--form svg {
    width: 95px;
    height: 95px;
  }

  .progress__result-note--catalog {
    display: block;
    color: #000000;
    opacity: 0.4;
  }
  .progress__result-note--catalog::before {
    background-color: #000000;
    opacity: 0.2;
  }
}
@media (min-width: 1440px) {
  .progress__result--form {
    top: 55%;
  }

  .progress__result-num--form {
    color: #ffffff;
  }

  .progress__result-note--form {
    color: #ffffff;
  }
  .progress__result-note--form::before {
    height: 2px;
  }
}
.flags-list {
  margin: 0;
  padding: 0;
  list-style: none;
  max-width: 270px;
}
.flags-list--catalog {
  min-height: 65px;
  padding: 0 5px;
}

.flags-list__item {
  width: 35px;
  height: 25px;
}

.flags-list__item span {
  display: inline-block;
  vertical-align: bottom;
  font-weight: 500;
  font-size: 15px;
  line-height: 1;
  color: #1d2e5b;
  text-transform: uppercase;
}

.flags-list--catalog .flags-list__item {
  width: 100%;
  margin-bottom: 4px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.flags-list--catalog .flags-list__item img {
  width: 26px;
  height: 18px;
  margin-right: 14px;
}

@media (min-width: 768px) {
  .flags-list--catalog {
    max-width: 490px;
    min-height: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(3, auto);
  }

  .flags-list--catalog .flags-list__item img {
    width: 35px;
    height: 24px;
    margin-right: 14px;
  }

  .flags-list--catalog .flags-list__item span {
    font-size: 20px;
  }
}
@media (min-width: 1440px) {
  .flags-list--catalog {
    display: flex;
    flex-direction: column;
  }

  .flags-list--catalog .flags-list__item {
    width: 100%;
    margin-bottom: 15px;
  }

  .flags-list--catalog .flags-list__item img {
    margin-right: 18px;
  }
}
.menu-toggle {
  position: absolute;
  top: 26px;
  right: 26px;
  width: 25px;
  height: 11px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 3;
  transition: 0.3s ease;
  display: none;
}
.menu-toggle::before, .menu-toggle::after {
  position: absolute;
  right: 0;
  width: 25px;
  height: 3px;
  border-radius: 1.5px;
  background-color: #cccccc;
  content: "";
}
.menu-toggle::before {
  top: 0;
}
.menu-toggle::after {
  bottom: 0;
}

.menu-toggle.active {
  top: 27px;
  height: 20px;
}
.menu-toggle.active::before {
  top: 7px;
  transform: rotate(45deg);
}
.menu-toggle.active::after {
  top: 7px;
  transform: rotate(-45deg);
}

@media (min-width: 768px) {
  .menu-toggle,
.menu-toggle.active {
    top: 60px;
    left: 48.5%;
  }
}
@media (min-width: 1440px) {
  .menu-toggle {
    display: none;
  }
}
.header {
  width: 100%;
  max-width: 1440px;
  background-color: #ffffff;
}

.header--js {
  background-color: #161c35;
}

.header--inner {
  background-color: #182044;
}

.header--js .menu-toggle {
  display: block;
}

.header--scroll {
  position: fixed;
  background-color: #ffffff;
  transition: all 0.7s ease;
  z-index: 60;
}

.header__container {
  position: relative;
  padding-top: 73px;
  z-index: 50;
}

.header__navigation {
  position: relative;
  width: 100%;
  padding: 44px 25px 39px 25px;
  background-color: #ffffff;
}

.header--js .header__navigation {
  position: absolute;
}

.header__navigation--closed {
  display: none;
}

.header--opened,
.header--opened .header__container {
  background-color: #ffffff;
}

.header--js .logo__link--header svg {
  fill: #ffffff;
}

.header--opened .logo__link--header svg {
  fill: #192144;
}

.header--opened .logo {
  top: 27px;
}

.header--scroll .logo {
  top: 26px;
}

.header--scroll .logo__link--header svg {
  fill: #192144;
}

@media (min-width: 768px) {
  .header__navigation {
    padding: 52px 96px 89px;
  }

  .header__navigation--closed {
    display: block;
    padding: 0;
  }

  .header__navigation--closed .navigation__auth-link,
.header__navigation--closed .main-menu,
.header__navigation--closed .user-menu,
.header__navigation--closed .social {
    display: none;
  }

  .header--js {
    background-color: #182044;
  }

  .header--js .navigation__auth-link {
    display: block;
  }

  .header__container {
    padding-top: 132px;
  }

  .header--opened {
    background-color: #ffffff;
  }

  .header--opened .logo {
    top: 45px;
  }

  .header--scroll {
    background-color: #ffffff;
  }

  .header--scroll .logo {
    top: 46px;
  }
}
@media (min-width: 1440px) {
  .header {
    background-color: #161c35;
  }

  .header--js {
    background-color: #161c35;
  }

  .header--scroll {
    background-color: #ffffff;
  }

  .header__container {
    padding: 0 60px 0;
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }

  .header--js .header__navigation {
    position: relative;
  }

  .header__navigation {
    position: relative;
    padding: 56px 0 92px 200px;
    background-color: transparent;
  }
  .header__navigation--inner {
    padding: 47px 0 78px 200px;
  }

  .header__navigation--closed {
    position: relative;
    display: block;
  }

  .header__navigation--closed .navigation__auth-link {
    display: none;
  }

  .header__navigation--closed .main-menu {
    display: flex;
  }

  .header--scroll .header__navigation {
    padding: 27px 0 31px 200px;
  }
  .header--scroll .header__navigation--inner {
    padding: 20px 0 20px 200px;
  }

  .header--scroll .main-menu__link {
    color: #192144;
  }

  .header--scroll .main-menu::before {
    width: 44px;
    top: 17px;
    left: -90px;
    background-color: #192144;
  }

  .header--scroll .logo {
    top: 20px;
  }

  .header--js .menu-toggle {
    display: none;
  }
}
.logo__link {
  display: block;
  outline: none;
}
.logo__link:hover, .logo__link:focus {
  outline: 1px solid #ffbd30;
  border-radius: 4px;
}

.logo--header {
  position: absolute;
  top: 20px;
  left: 26px;
  z-index: 1;
}

.logo--footer {
  position: relative;
}

.logo__link img {
  display: none;
}

.logo__link svg {
  width: 96px;
  fill: #192144;
}
.logo__link svg--header svg {
  fill: #192144;
}

@media (min-width: 768px) {
  .logo--header {
    top: 45px;
    left: 36px;
  }

  .logo__link img {
    display: block;
    width: 166px;
    height: 42px;
  }

  .logo__link svg {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: -5px;
    width: 106px;
    height: 16px;
  }
}
@media (min-width: 1440px) {
  .logo {
    width: 200px;
    left: 55px;
  }

  .logo__link img {
    width: 200px;
    height: 50px;
  }

  .logo__link svg {
    right: -6px;
    width: 128px;
    height: 20px;
  }
  .logo__link svg--header svg {
    fill: #ffffff;
  }
}
.user-menu {
  margin: 0;
  padding: 0;
  list-style: none;
}

.user-menu__link {
  position: relative;
  display: flex;
  align-items: center;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.1;
  color: #192144;
  text-transform: uppercase;
  outline: none;
}
.user-menu__link:hover, .user-menu__link:focus {
  color: #ffbd30;
}
.user-menu__link:active svg, .user-menu__link:active span {
  opacity: 0.3;
}
.user-menu__link::before {
  width: 45px;
  height: 45px;
  background-color: #121936;
  border-radius: 50%;
  content: "";
}

.user-menu__link--feedback {
  color: #ffd74b;
}

.user-menu__link:hover svg,
.user-menu__link:focus svg {
  fill: #ffbd30;
}

.user-menu__link span {
  margin-left: 15px;
}

.user-menu__link svg {
  position: absolute;
  top: 14px;
  left: 14px;
  fill: #ffd74b;
}

.user-menu__link--phone svg {
  width: 18px;
  height: 17px;
}

.user-menu__link--mail svg {
  width: 18px;
  height: 13px;
}

@media (min-width: 768px) {
  .user-menu__link {
    font-size: 28px;
  }
  .user-menu__link::before {
    width: 68px;
    height: 68px;
  }

  .user-menu__link span {
    margin-left: 23px;
  }

  .user-menu__link svg {
    top: 22px;
    left: 21px;
  }

  .user-menu__link--phone svg {
    width: 27px;
    height: 26px;
  }

  .user-menu__link--mail svg {
    width: 27px;
    height: 19px;
  }
}
@media (min-width: 1440px) {
  .user-menu--feedback .user-menu__link--phone svg {
    top: 23px;
    left: 27px;
    width: 36px;
